body {
  margin: 0;
  background-color: antiquewhite;
}

.main {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 50px;
  row-gap: 50px;
  padding: 70px;
}
.content{

  margin: 10px;
  padding: 0px;
  border-style: solid;
  background-color: aliceblue;
}
.content-item{
  display: inline-block;
  padding: 20px;
}
.search-form{
  margin: 50px;
  background-color: grey;
  padding-top: 30px;
  padding-right: 30px;
  padding-bottom: 30px;
  padding-left: 80px;
  display: block;
}
.input-field{
  margin: 5px;
  width: 40%;
  padding: 12px;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.search-button{
  width: 10%;
  padding: 12px 20px;
  background-color: rgb(202, 174, 174);
  border: 1px solid rgb(78, 62, 62);
}
.search-button:hover {background-color: #99ac4b;}
.search-button:active {background-color:rgb(202, 174, 174);}

.detail-button{
  width: 100%;
}

.ShowDetail {
  background-color: rgb(180, 121, 43);
  margin-top: 200px;
  margin-left: 30em;  
  padding-top: 10px;
  width: 40%;
  height: 400px;
  display: block;
}

.p1 {
  font-weight: bold;
  text-align: center;
}

.p2{
  padding-left: 12em;
}

.detail-btn {
  margin-top: 5em;
  margin-left: 20em;
  padding: 20px;
  width: 30%;
  background-color: rgb(193, 197, 78);
}
.detail-btn2{
  width: 10%;
  padding: 12px 20px;
  background-color: rgb(202, 174, 174);
  border: 1px solid rgb(78, 62, 62);
}

.search-dropbtn {
  background-color: rgb(202, 174, 174);
  border: 1px solid rgb(78, 62, 62);
  color: white;
  padding: 12px 20px;
  font-size: 16px;
  border: none;
}
.search-dropdown{
  margin-top: 20px;
  margin-left: 10px;
  margin-right: 10px;
  display: inline-block;
}

.search-dropdown-content {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.search-dropdown-content p:hover {background-color: #ddd;}

.search-dropdown:hover .dropdown-content {display: block;}

.search-dropdown:hover .search-dropbtn {background-color: #99ac4b;}

.dropbtn {
  background-color: #04AA6D;
  color: white;
  padding: 16px;
  font-size: 16px;
  border: none;
}
.dropdown {
  margin-left: 50px;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown-content p {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-content p:hover {background-color: #ddd;}

.dropdown:hover .dropdown-content {display: block;}

.dropdown:hover .dropbtn {background-color: #3e8e41;}